<script>
  import InformationScreen from './components/InformationScreen.svelte'
  import Loader from './components/Loader.svelte'
  import Button from './components/Button.svelte'

  import { serviceWorkerStatus } from './stores/devtoolsMessageObservable.ts'

  function onContinue() {
    serviceWorkerStatus.set('available')
  }
  let reloading = false
  function onReload() {
    reloading = true
    window.location.reload()
  }
</script>

<InformationScreen title="Installation completed">
  <p>Devtools is now enabled.</p>
  <p>
    While getting ready, it missed the HTTP request for this page and related assets. Do you want to
    reload the page to start again?
  </p>
  <div class="buttons-bziE3H">
    <Button disabled={reloading} onClick={onReload}>{reloading ? 'RELOADING...' : 'RELOAD'}</Button>
    <Button onClick={onContinue}>CONTINUE</Button>
  </div>
</InformationScreen>

<style>
  /* !Important: read NOTES_ABOUT_STYLE in 000_Main.svelte */
  :global(div.buttons-bziE3H) {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-top: 50px;
  }
</style>
