<script>
  import Section from '../components/Section.svelte'
  import BrowserCacheItem from './BrowserCacheItem.svelte'
  import Field from '../components/Field.svelte'
  import Label from '../components/Label.svelte'
  import Value from '../components/Value.svelte'
  import Tooltip from '../components/Tooltip.svelte'

  import { sessionStats } from '../stores/statsStore'
  import { items } from '../stores/itemsStore'
  import { open } from '../stores/appStateStore'

  let el

  $: {
    $items // scroll to the top whenever $items change
    if (el) el.scrollTop = 0
  }
</script>

<Section title="Browser" titleCls="edgio-devtools-color-browser">
  <div style="flex: 1">
    <Tooltip title="Browser Hits">
      <div slot="target">
        <Field>
          <Label>hits:</Label>
          <Value variant="default">{$sessionStats.browser || 0}</Value>
          <Value variant="success">[{$sessionStats.browserPercent || 0}%]</Value>
        </Field>
      </div>
      <div slot="content">
        The number of requests served from the browser cache and the percentage of the total number
        of requests they represent.
      </div>
    </Tooltip>
  </div>
  <div style="flex: 1">
    <Tooltip title="Prefetch Ratio">
      <div slot="target">
        <Field>
          <Label>Prefetch:</Label>
          <Value variant="success">
            {$sessionStats.prefetchHitCount || 0} / {$sessionStats.prefetchRequestCount || 0}
          </Value>
        </Field>
      </div>
      <div slot="content" style="width:400px">
        <div>The number of successful prefetches / total prefetches</div>
        <br />
        <div>
          Prefetch requests will be rejected with status code 412 if they cannot be fulfilled from
          Edgio edge cache. As more users visit the site, the edge cache fills up and more
          prefetches are successful.
        </div>
      </div>
    </Tooltip>
  </div>
</Section>

<div class="cache-items-list-JBAvWf" bind:this={el}>
  {#each $items as item (item.id)}
    <!-- We don't animate when closed as it makes balls jump out of the container -->
    <BrowserCacheItem animate={$open} index={$items.length - item.id - 1} {item} />
  {/each}
</div>

<style>
  :global(div.cache-items-list-JBAvWf) {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 4px 12px 0 12px;
    overflow-y: auto;
    overflow-x: hidden;
    margin-block-start: 0;
    margin-block-end: 0;
  }
</style>
