<script>
  import clsx from 'clsx'

  let enabled = false
  let local = false

  if (/127\.0\.0\.1|^localhost(:\d+)?$/.test(window.location.host)) {
    fetch('/__edgio__/caching')
      .then(res => res.json())
      .then(res => {
        enabled = res.enabled
        local = res.local
      })
  }

  function toggle() {
    enabled = !enabled
    // Telling devtools router to update local edge caching state
    fetch(`/__edgio__/caching?enabled=${enabled}`, { method: 'post' })
  }
</script>

{#if local}
  <div class={clsx('container-RK_h4m', { 'enabled-k74TkX': enabled })}>
    <div class="status-+mc4Kq">Dev edge cache is {enabled ? 'enabled' : 'disabled'}</div>
    <button on:click={toggle}>{enabled ? 'Disable' : 'Enable'}</button>
  </div>
{/if}

<style>
  :global(div.container-RK_h4m) {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 8px 0 8px 0;
    font-size: 12px;
    background: #f44336;
  }

  :global(div.enabled-k74TkX) {
    background: #4caf50;
  }

  :global(div.status-+mc4Kq) {
    text-transform: uppercase;
  }

  button {
    flex: 0.5;
    margin-left: 10px;
    padding: 8px 0;
    text-align: center;
    background-color: white;
    color: #222b4c;
    font-weight: bold;
    border-radius: 25px;
    text-transform: uppercase;
    font-size: 10px;
    cursor: pointer;
    opacity: 0.8;
  }

  button:hover {
    opacity: 0.9;
  }
</style>
