<script>
  import formatTTL from './formatTTL'
  import formatCachingStatus from './formatCachingStatus'

  export let item

  const servedFrom = {
    edge: 'EDGE CACHE',
    browser: 'BROWSER CACHE',
    server: 'SERVERLESS',
  }

  function getTTL() {
    const ttl = []
    if (item.routeConfig.edgeMaxAgeSeconds) {
      ttl.push('Edge: ' + formatTTL(item.routeConfig.edgeMaxAgeSeconds))
    }
    if (item.routeConfig.maxAgeSeconds) {
      ttl.push('Browser: ' + formatTTL(item.routeConfig.maxAgeSeconds))
    }
    return ttl.join(', ')
  }
</script>

<div class="details-item-MlrR0e">
  <span class="details-title-QLIur4">URL:</span>
  <pre>{item.path}</pre>
</div>
<div class="details-item-MlrR0e">
  {#if item.source === 'edge-412'}
    <span class="details-title-QLIur4">Status:</span><span class={`edgio-devtools-color-server`}
      >Prefetching was unsuccessful because the response was not found in the edge cache.</span
    >
  {:else}
    <span class="details-title-QLIur4">Served From:</span><span
      class={`edgio-devtools-color-${item.source}`}>{servedFrom[item.source]}</span
    >
  {/if}
</div>
{#if item.routeConfig}
  <div class="details-item-MlrR0e">
    <span class="details-title-QLIur4">Route:</span>
    <pre>match({item.routeConfig.criteriaPath || item.routeConfig.pattern})</pre>
  </div>
  {#if item.routeConfig.edgeMaxAgeSeconds || item.routeConfig.maxAgeSeconds}
    <div class="details-item-MlrR0e">
      <span class="details-title-QLIur4">Cache TTL:</span>
      <span>{getTTL()}</span>
    </div>
  {/if}
{/if}
{#if !item.routeConfig}
  <div class="details-item-MlrR0e"><span class="details-title-QLIur4">Route:</span>no matching route</div>
{/if}
{#if item.cacheStatus}
  <div class="details-item-MlrR0e">
    <span class="details-title-QLIur4">Edge Cache Status:</span>{formatCachingStatus(item.cacheStatus)}
  </div>
{/if}

<style>
  /* !Important: read NOTES_ABOUT_STYLE in 000_Main.svelte */
  :global(span.details-title-QLIur4) {
    color: lightgrey;
    text-transform: uppercase;
    margin-right: 4px;
    display: block;
  }
  :global(div.details-item-MlrR0e:first-child) {
    margin-top: 0;
  }
  :global(div.details-item-MlrR0e) {
    margin-top: 1em;
  }
  pre {
    white-space: pre-wrap;
    display: inline;
  }
</style>
