<script>
  import Content from './Content.svelte'
  import { createEventDispatcher } from 'svelte'

  const DEFAULT_ZINDEX = 1000
  let targetRef
  let timeout
  const dispatch = createEventDispatcher()

  const schedule =
    fn =>
    (...args) => {
      if (timeout) {
        clearTimeout(timeout)
        timeout = null
      }
      timeout = setTimeout(() => {
        fn(...args)
        timeout = null
      }, delay)
    }

  const onOpen = () => {
    dispatch('open')
  }

  export let action = 'click'
  export let zIndex = DEFAULT_ZINDEX
  export let arrow = true
  export let placement = 'auto'
  export let arrowColor = ''
  export let overlayColor
  export let preventDefault = false
  export let stopPropagation = false
  export let open = false
  export let delay = 300

  const setOpen = schedule(() => {
    open = !open

    if (!open) {
      dispatch('close')
    }
  })

  const eventClick = e => {
    if (preventDefault) e.preventDefault()
    if (stopPropagation) e.stopPropagation()
    setOpen()
  }

  const eventMouseOut = schedule(({ relatedTarget }) => {
    if (relatedTarget.id === 'edgio-devtools-popover-overlay' && !open) {
      setOpen()
    }
  })

  const onTouchEnd = action === 'click' ? eventClick : null
  const onClick = action === 'click' ? eventClick : null
  const setOpenTrue = schedule(() => (open = true))
  const onMouseOver = action === 'hover' ? setOpenTrue : null
  const onMouseOut = action === 'hover' ? eventMouseOut : null
</script>

<div class="popover-l37wHl">
  <div
    bind:this={targetRef}
    class="target-8iCpHy"
    style={open ? `z-index: ${zIndex + 10}` : ''}
    on:click={onClick}
    on:touchend={onTouchEnd}
    on:mouseover={onMouseOver}
    on:mouseout={onMouseOut}
  >
    <slot name="target" {open} />
  </div>
  {#if open}
    <Content
      on:open={onOpen}
      on:setOpen={setOpen}
      {placement}
      {targetRef}
      {zIndex}
      {arrow}
      {action}
      {overlayColor}
      {arrowColor}
      {preventDefault}
      {stopPropagation}
    >
      <slot name="content" {open} />
    </Content>
  {/if}
</div>

<style>
  /* !Important: read NOTES_ABOUT_STYLE in 000_Main.svelte */
  :global(div.target-8iCpHy) {
    position: relative;
  }
  :global(div.popover-l37wHl) {
    position: relative;
  }
</style>
