<script>
  import WelcomeScreen from './WelcomeScreen.svelte'
  import LoadingScreen from './LoadingScreen.svelte'
  import UnavailableScreen from './UnavailableScreen.svelte'
  import ReloadingScreen from './ReloadingScreen.svelte'
  import StatsScreen from './StatsScreen.svelte'
  import Header from './Header.svelte'
  import Footer from './Footer.svelte'
  import { serviceWorkerStatus } from './stores/devtoolsMessageObservable.ts'
  import { open } from './stores/appStateStore'
  import Toast from './Toast.svelte'

  let welcomed = localStorage.getItem('edgio-devtools-welcomed') === 'true'
  function onWelcomeConfirmed() {
    welcomed = true
    localStorage.setItem('edgio-devtools-welcomed', 'true')
  }
</script>

<!-- This ID is used to detect that a DOM node belongs to the devtools -->
<div class="aside-DppKx0" class:open-Rr0vsa={$open} class:closed-0gLzTn={!$open} id="edgio-devtools-main-div">
  <div class="full-view-ldPJwz">
    <Header />
    <Toast />
    {#if $serviceWorkerStatus === 'loading'}
      <LoadingScreen />
    {:else if $serviceWorkerStatus === 'installed'}
      <ReloadingScreen />
    {:else if $serviceWorkerStatus === 'unavailable'}
      <UnavailableScreen />
    {:else if !welcomed}
      <WelcomeScreen onConfirm={onWelcomeConfirmed} />
    {:else}
      <StatsScreen />
    {/if}
  </div>
  <Footer />
</div>

<style>
  /* !Important: read NOTES_ABOUT_STYLE in 000_Main.svelte */
  :global(div.aside-DppKx0) {
    font-family: sans-serif;
    color: #eeeeee;
    position: fixed;
    right: 10px;
    bottom: 10px;
    box-sizing: border-box;
    background-color: rgba(6, 3, 28, 0.85);
    width: 350px;
    max-width: calc(100vw - 60px);
    border-radius: 5px;
    z-index: 999999;
  }

  :global(div.full-view-ldPJwz) {
    height: calc(100vh - 180px);
    max-height: 600px;
    display: flex;
    flex-direction: column;
    transition: 250ms ease-in-out;
    position: relative;
  }

  :global(div.aside-DppKx0.open-Rr0vsa) :global(div.full-view-ldPJwz) {
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14),
      0px 3px 14px 2px rgba(0, 0, 0, 0.3);
  }

  :global(div.aside-DppKx0.closed-0gLzTn) :global(div.full-view-ldPJwz) {
    height: 0px;
    overflow: hidden;
  }
</style>
