<script>
  import clearButtonObservable from './stores/clearButtonObservable'
  import { clearCache } from './stores/devtoolsMessageObservable'
  import CachingStatus from './CachingStatus.svelte'

  function onClearStats() {
    clearButtonObservable.publish('session')
  }

  function disable() {
    if (confirm('To re-enable the devtools, navigate to /__edgio__/devtools/enable')) {
      window.location = '/__edgio__/devtools/disable'
    }
  }
</script>

<div>
  <button on:click={onClearStats}>clear stats</button>
  <button on:click={clearCache}>clear browser cache</button>
  <button on:click={disable}>disable devtools</button>
</div>
<CachingStatus />

